<template lang="jade">
  .me-page
    slot(name="cover")
    slot(name="movebar")
    slot(name="resize-x")
    slot(name="resize-y")
    slot(name="toolbar")
    .width-draw-info.scroll-content

      .bank-form(v-if="stepIndex === 0")
        .notice(style="margin-top: .2rem")
          span.title 温馨提示：
          p.content
            | 每个游戏帐户最多绑定 
            span.text-danger 5
            |  张银行卡，您已成功绑定 
            span.text-danger {{ times }} 0
            |  张。
            br
            span.text-danger 银行卡锁定后，不能增加新的银行卡，同时也不能解绑已绑定的银行卡！
            br
            | 新绑定的提款银行卡需要绑定时间超过 
            span.text-danger 6
            |  小时才能正常提款

        .form
          .buttons(style="float: right; padding: .2rem 0")
            .ds-button.primary.large(@click="(type = 'bind') &&  stepIndex++") 增加银行卡
            .ds-button.cancel.large(@click=" (type = 'lock') &&  stepIndex++") 锁定全部银行卡

        el-table.header-bold(:data="myBanks" style="margin: .2rem")
          el-table-column(label="银行名称")
            template(scope="scope")
              .ds-icon-bank-card(v-bind:class=" [ scope.row.class ] " style="margin: 0")

          el-table-column(prop="cardNo" label="卡号")
          el-table-column(prop="bindTime" label="绑定时间")
          el-table-column(label="操作" min-width="60"  align="center")
            template(scope="scope")
              .ds-button.text-button.blue(@click="unbindCard(scope.row)") 解绑


      
      


      .bank-form(v-if="stepIndex === 1 && type === 'bind' ")
        
        .notice(style="margin-top: .2rem")
          span.title 温馨提示：
          p.content
            | 1. 银行卡绑定成功后，平台任何区域都
            span.text-danger  不会 
            | 出现您的完整银行帐号、开户姓名等信息。
            br
            | 2. 银行绑定成功扣，相关信息将无法查看与修改，只能进行
            span.text-danger  解除绑定 
            | 操作，请仔细填下表。
            br
            | 3. 每个游戏帐户最多绑定
            span.text-danger  5 
            | 张银行卡，您已成功绑定
            span.text-danger  2 
            | 张。
            br
            | 4. 一个游戏帐户只能绑定同一个开户人姓名的银行卡。


        p.title(style="padding: 0 .18rem 0 .4rem; margin: .2rem 0; color: #333") 您正在增加
          span.text-blue {{ nickName }} 星星110
          |  帐号的银行卡
          span.ds-button.text-button.blue(style="float: right" @click="stepIndex--") {{ '<返回上一页' }}
        StepTabs(type="text" v-bind:steps = "bs" v-bind:stepIndex="bi")
        
        hr(style="height: 0; border: 0; border-top: 1px solid #ccc; margin: .2rem .4rem 0 .4rem")

        .form(style="margin: .2rem .4rem" v-if="bi === 0")
         
          p.item 开户银行：&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            el-select(v-model="bank")
              el-option(v-for="(b, index) in BANKS" v-bind:label="b.text" v-bind:value="b")

          p.item 开户银行省份：
            el-select(v-model="province")
              el-option(v-for="(p, index) in PROVINCES" v-bind:label="p.text" v-bind:value="p")
          
          p.item 开户银行城市：
            el-select(v-model="city")
              el-option(v-for="(c, index) in province.citys" v-bind:label="c.text" v-bind:value="c")
          
          p.item 支行名称：&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            input.ds-input.large
            span(style="font-size: .12rem")  （由1至20个字符或汉字组成，不能使用特殊字符）

          p.item 开户人姓名：&nbsp;&nbsp;&nbsp;
            input.ds-input.large
            span(style="font-size: .12rem")  （由1至20个字符或汉字组成，不能使用特殊字符）

          p.item 银行帐号：&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            input.ds-input.large
            span(style="font-size: .12rem")  （银行帐号由16位、18位或19位数字组成）

          p.item 确认银行帐号：
            input.ds-input.large
            span(style="font-size: .12rem")  （银行帐号只能手动输入，不能粘贴）

          .buttons(style="margin-left: 1rem; padding-top: .05rem")
            .ds-button.primary.large(@click="bi++") 添加


        .form(style="margin: .2rem .4rem" v-if="bi === 1")

          .item(style="line-height: .5rem") 开户银行：&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            p.banks(style="color: #333")
              span.ds-icon-bank-card(v-bind:class=" [ bank.class ] ")

            p.item 开户银行省份：{{ province.text }}
            p.item 开户银行城市：{{ city.text }}
            p.item 支行名称：&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{  }} bankName
            p.item 开户人姓名：&nbsp;&nbsp;&nbsp;{{ }} 开户人
            p.item 银行帐号：&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
              span(style="color: #333") {{ }}********3133
         
          .buttons(style="margin-left: .95rem; padding-top: .05rem")
            .ds-button.primary.large(@click="bindNow") 立即绑定



      .bank-form(v-if="stepIndex === 1 && type === 'unbind' ")
        p.title(style="padding: 0 .18rem 0 .4rem; margin: .2rem 0; color: #333") 您正在解绑银行卡
          span.ds-button.text-button.blue(style="float: right" @click="stepIndex--") {{ '<返回上一页' }}
        StepTabs(type="text" v-bind:steps = "s" v-bind:stepIndex="i")
        
        hr(style="height: 0; border: 0; border-top: 1px solid #ccc; margin: .2rem .4rem 0 .4rem")

        .form(style="margin: .2rem .4rem" v-if="i === 0")
          .item(style="line-height: .5rem") 已绑定的银行卡：
            p.banks(style="color: #333")
              span.ds-icon-bank-card(v-bind:class=" [ bank.class ] ")
              | *********0000
          
          p.item 银行帐号：&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            input.ds-input.large
            span(style="font-size: .12rem")  （请输入对应绑定卡的银行卡号）
          
          p.item 开户人姓名：&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            input.ds-input.large
            span(style="font-size: .12rem")  （请输入对应绑定卡的开户名）

          .buttons(style="margin-left: 1.15rem; padding-top: .05rem")
            .ds-button.primary.large(@click="i++") 下一步

        .form(style="margin: .2rem" v-if="i === 1")
          .item(style="line-height: .5rem") 开户银行：
            p.banks(style="color: #333")
              span.ds-icon-bank-card(v-bind:class=" [ bank.class ] ")
          
          p.item 银行帐号：&nbsp;{{ }}********3133
          p.item 绑定时间：&nbsp;{{ }}2015-15-12 12:01:56
          p.item 资金密码：&nbsp;
            input.ds-input.large
        
          .buttons(style="margin-left: 0.7rem; padding-top: .05rem")
            .ds-button.primary.large(@click="unbindNow") 解绑


      .bank-form(v-if="stepIndex === 1 && type === 'lock' ")
        .notice(style="margin-top: .2rem")
          span.title 温馨提示：
          p.content
            | 1. 
            span.text-danger 银行卡锁定后，不能增加新的银行卡，同时也不能解绑已绑定的银行卡；
            br
            | 2. 银行卡锁定，可以一定程度增强您的帐户安全。
            br
            | 例：帐户被他人盗用后，由于此功能的限制，您帐户的资金不会被他人提现。
            br
            与此同时，客服不提供帐户银行卡解除锁定功能，所以：
            span.text-danger 锁定前请自行斟酌。



        p.title(style="padding: 0 .18rem 0 .4rem; margin: .2rem 0; color: #333") 您正在锁定
          span.text-blue {{ nickName }} 星星110
          |  帐号的银行卡
          span.ds-button.text-button.blue(style="float: right" @click="stepIndex--") {{ '<返回上一页' }}

        hr(style="height: 0; border: 0; border-top: 1px solid #ccc; margin: .2rem .4rem 0 .4rem")

        .form(style="margin: .2rem .4rem" v-if="i === 0")

          .item(style="line-height: .5rem") 银行卡：&nbsp;&nbsp;&nbsp;
            div.banks(style="color: #333; display: inline-block")
              div(v-for=" bank in myBanks ")
                .ds-icon-bank-card(v-bind:class=" [ bank.class ] ") 
                span(style="color: #333")  {{ }}*********0000
          
          
          p.item 资金密码： 
            input.ds-input.large

          .buttons(style="margin-left: .75rem; padding-top: .05rem")
            .ds-button.primary.large(@click="lockNow") 立即锁定


</template>

<script>
import StepTabs from 'components/StepTabs'
import { BANKS, PROVINCES } from '../../util/static'
import api from '../../http/api'
// import store from '../../store'
export default {
  data () {
    return {
      BANKS: BANKS,
      bank: {},

      PROVINCES: PROVINCES,
      province: {},
      city: {},

      type: 'bind',
      stepIndex: 0,
      s: ['01 资料验证', '02 安全验证', '03 完成'],
      i: 0,

      bs: ['01 添加新银行卡信息', '02 核对信息', '03 完成'],
      bi: 0
    }
  },
  computed: {
    textMoney () {
      return this.money
    }
  },
  created () {
    this.myBanks = [
      {id: 1, class: 'zggsh', cardNo: 112132484244, bindTime: '1990-02-02 12:56:05', text: '中国工商银行'},
      {id: 1, class: 'zggsh', cardNo: 112132484244, bindTime: '1990-02-02 12:56:05', text: '中国工商银行'}
    ]
    this.getUserBankCards()
  },
  methods: {
    bindNow () {
    },
    unbindCard (row) {
      this.$modal.warn({
        content: '该银行卡解绑后，不能再绑定到其它帐号上！',
        target: this.$el,
        ok () {
          this.type = 'unbind'
          this.stepIndex++
          this.bank = row
        },
        O: this
      })
    },
    unbindNow () {
      this.__loading({
        text: '解绑中...',
        target: this.$el
      }, 10000)
      setTimeout(() => {
        this.$modal.success({
          target: this.$el,
          content: '恭喜您，解绑成功！',
          close () {
            this.stepIndex = 0
            this.i = 0
          },
          O: this
        })
      }, 10000)
    },
    lockNow () {
      let modal = this.$modal.warn({
        content: '银行卡锁定后，将无法再增加、删除银行卡！',
        target: this.$el,
        btn: ['立即锁定'],
        ok () {
          modal.type = 'success'
          modal.content = '恭喜您，锁定成功！'
          return false
        },
        O: this
      })
    },
    getUserBankCards () {
      this.$http.get(api.getUserBankCards).then(({data}) => {
        if (data.success === 1) {
        }
      }).catch(rpe => {
      })
    }
  },
  components: {
    StepTabs
  }
}
</script>

<style lang="stylus" scoped>
  @import '../../var.stylus'
  .scroll-content
    top TH
  .width-draw-info
    .cashpwd-form
      padding-top .1rem
  
  .notice
    margin 0 .2rem
    padding PWX
    background-color #fffde8
    border 1px solid #d5d09b
    radius()
    .content
      display inline-block
      margin 0
      line-height .25rem
      vertical-align top
  .form
    margin 0 .4rem
    .amount
      font-family Roboto
      font-size 0.5rem
  .banks
    display inline-block
    margin 0
    max-width 90%
    vertical-align top
  .ds-radio-label
    padding 0 .1rem
  .ds-icon-bank-card
    width 167px
    height 40px
    margin .05rem 
    radius()
    display inline-block

    vertical-align middle
  .buttons
    .ds-button:not(:first-child)
      margin-left .1rem
</style>


