<template lang="jade">
  .me-page
    slot(name="cover")
    slot(name="movebar")
    slot(name="resize-x")
    slot(name="resize-y")
    slot(name="toolbar")
    .width-draw-info.scroll-content(style="padding-top: .2rem")
      // .tabs(style="text-align: center")
        // .ds-button-group
          // .ds-button.text-button.large(v-bind:class="{selected: tabIndex === 1}" @click="tabIndex = 1") 提现申请
          // .ds-button.text-button.large(v-bind:class="{selected: tabIndex === 2}" @click="tabIndex = 2") 提现记录

      .cashpwd-form.form(v-if="tabIndex === 1 && stepIndex === 0")
        p 资金密码：
          input.ds-input.large(v-model="cpwd" type="password" @keyup.enter="checkSecurityPwd")
          .buttons(style="margin-left: .70rem")
            .ds-button.primary.large(@click="checkSecurityPwd") 确认
      

      .bank-form(v-if="tabIndex === 1 && stepIndex === 1")
        .notice
          span.title 温馨提示：
          p.content
            | 每天限提 
            span.text-danger 5
            |  次，今天您已经发起了 
            span.text-danger {{ times }} 0
            |  次提现申请。
            br
            | 每天的提现处理时间为：
            span.text-danger 早上 10:00 至 次日凌晨 2:00
            br
            | 新绑定的提款银行卡需要绑定时间超过 
            span.text-danger 6
            |  小时才能正常提款
        .form
          p.item 可提现金额：
            span.amount {{ me.money }}

          .item(style="line-height: .5rem") 收款银行卡：
            p.banks
              label.ds-radio-label(v-for="bank in banksO" @click="selectBank = bank")
                span.ds-radio.white(v-bind:class="{ active: selectBank.id === bank.id }")
                span.ds-icon-bank-card(v-bind:class=" [ bank.class ] ")

              span.ds-button.text-button.blue.el-icon-caret-bottom(v-if="!showAllBank" @click="showAllBank = true")  更多银行

          p.item(style="padding: .1rem 0") 提现限额：&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;(单笔提现限额：最低：
            span.min {{  }} 903,394.55 
            | 元，
            | 最高：
            span.min {{  }} 903,394.55 
            | 元)

          p.item 提现金额：&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            input.ds-input.large(v-model="money")
            span(style="color: #999; padding-left: .1rem") {{ textMoney }}

          .buttons(style="margin-left: .98rem")
            .ds-button.primary.large(@click="stepIndex++") 确认

      
      .bank-form(v-if="tabIndex === 1 && stepIndex === 2")
        
        .form
          p.item 实扣金额：&nbsp;
            span.amount {{  }} 903,394.55

          p.item 到帐金额：&nbsp;
            span.amount {{  }} 903,394.55

          .item(style="line-height: .5rem") 开户银行：&nbsp;&nbsp;&nbsp;
            p.banks
              span.ds-icon-bank-card(v-bind:class=" [ selectBank.class ] ")



          p.item 银行卡帐号：{{ cardNo }} ***1362852182
            

          .buttons(style="margin-left: .85rem")
            .ds-button.primary.large(@click="withDrawNow") 提交
</template>

<script>
import api from '../../http/api'
import store from '../../store'
export default {
  data () {
    return {
      me: store.state.user,
      stepIndex: 0,
      cpwd: '',
      // x
      tabIndex: 1,
      banks: [
        {id: 1, class: 'zggsh'},
        {id: 2, class: 'zggsh'},
        {id: 3, class: 'zggsh'},
        {id: 4, class: 'zggsh'},
        {id: 5, class: 'zggsh'},
        {id: 6, class: 'zggsh'}
      ],
      selectBank: {id: 1, class: 'zggsh'},
      showAllBank: false,
      money: 0

    }
  },
  computed: {
    banksO () {
      return this.showAllBank ? this.banks : this.banks.slice(0, 2)
    },
    textMoney () {
      return this.money
    }
  },
  methods: {
    withDrawNow () {
      this.$modal.success({
        content: '恭喜您，提交成功！',
        target: this.$el
      })
    },
    checkSecurityPwd () {
      this.$http.post(api.checkSecurityPwd, {password: this.cpwd}).then(({data}) => {
        if (data.success === 1) {
          this.stepIndex++
          this.__setCall({fn: '__getUserFund'})
          this.getUserBankCards()
        }
      }).catch(rpe => {
      })
    },
    getUserBankCards () {
      this.$http.get(api.getUserBankCards).then(({data}) => {
        if (data.success === 1) {
        }
      }).catch(rpe => {
      })
    }
  },
  components: {
  }
}
</script>

<style lang="stylus" scoped>
  @import '../../var.stylus'
  .scroll-content
    top TH
  .width-draw-info
    .cashpwd-form
      padding-top .1rem
  
  .notice
    margin 0 .2rem
    padding PWX
    background-color #fffde8
    border 1px solid #d5d09b
    radius()
    .content
      display inline-block
      margin 0
      line-height .25rem
      vertical-align top
  .form
    margin 0 .4rem
    .amount
      font-family Roboto
      font-size 0.5rem
  .banks
    display inline-block
    margin 0
    max-width 90%
    vertical-align top
  .ds-radio-label
    padding 0 .1rem
  .ds-icon-bank-card
    width 167px
    height 40px
    margin .05rem 
    radius()
    display inline-block

    vertical-align middle
</style>


