<template lang="jade">

  transition(name="fade" appear=true)

    .modal(:class="[customClass]")

      .mask(v-if="modal")

      .box-wrapper

        .box

          .header-bar(v-if="title || showclose")

            .title {{ title }} 

            .buttons
              i.el-icon-close.ds-button.text-button(@click="_close" )


          .content 

            .ds-icon-success-large(v-if="type === 'success'") {{ content }}

            .ds-icon-notice-large(v-if="type === 'warn'") {{ content }}

            .default(v-if="type === 'confirm'") {{ content }}


          .footer-bar

            .ds-button.large.success(@click="_ok" v-if="type === 'success' ") {{ btn[0] || '确定' }}

            .ds-button.danger.ok(@click="_ok" v-if="type === 'warn' ") {{ btn[0] || '确定' }}
            .ds-button.cancel(@click="_cancel"  v-if="type === 'warn' ") 我再想想

            .ds-button.primary.ok(@click="_ok" v-if="showok") {{ btn[0] || '确定' }}
            .ds-button.cancel(@click="_cancel"  v-if="showcancel") 取消



      
</template>

<script>
  export default {
    name: 'Modal',
    data () {
      return {
        type: 'confirm',
        title: '',
        content: '',
        btn: [],
        showclose: true,
        showok: true,
        showcancel: true,
        modal: true,
        ok: null,
        O: null
      }
    },
    methods: {
      _close () {
        if (typeof this.close === 'function') {
          if (this.close.call(this.O) === false) return
        }
        this.$el &&
        this.$el.parentNode &&
        this.$el.parentNode.removeChild(this.$el)
        this.$destroy()
      },
      _ok () {
        if (typeof this.ok === 'function') this.ok.call(this.O) !== false && this._close()
        else this._close()
      },
      _cancel () {
        if (typeof this.cancel === 'function') this.cancel.call(this.O) !== false && this._close()
        else this._close()
      }
    },
    created () {
      if (this.type !== 'confirm') {
        this.showcancel = false
        this.showok = false
      }
    }
  }
</script>


<style lang="stylus" scoped>
  @import '../var.stylus' 
  .modal 
    position absolute
    top 0
    bottom 0
    left 0
    right 0
    text-align center
    z-index 9999
    .mask
      position absolute
      left 0
      top 0
      width 100%
      height 100%
      opacity .5
      background #000
      z-index 9998
    .box-wrapper
      position absolute
      top 0
      bottom 0
      left 0
      right 0
      text-align center
      z-index 9999
      &:after
        content ''
        height 100%
        width 0
        vertical-align middle
        display inline-block
    .box
      text-align left
      display inline-block
      vertical-align middle
      background-color #fff
      width 4.2rem
      radius()
      font-size 16px
    .header-bar
      position relative
      height .5rem
      padding-left PW
      .title
        font-size .16rem
        font-weight 700
        color #333
        float left
        line-height .5rem
        max-width 50%
      .buttons
        float right
        max-width 50%
        .el-icon-close
          margin .08rem
          padding .05rem .1rem
          font-size .18rem
          color LIGHT
          radius()
          &:hover
            color #fff
            background-color DANGER-HOVER
          &:active
            color #fff
            background-color DANGER-ACTIVE
    .footer-bar
      padding .5rem  .2rem
      text-align center
      .ds-button:not(:first-child)
        margin-left .1rem
        
    .content
      position relative
      padding 0 .2rem
      font-size 14px

  .ds-button.success
    width 50%

  .ds-icon-success-large
  .ds-icon-notice-large
    padding-top 90px
    font-size .18rem
    text-align center
    color #333
</style>




